// 
export function IsLogin() {
	let openid = uni.getStorageSync('openid')
	let token = uni.getStorageSync('token')
	
	console.log('openid length:' + openid + ", token length:" + token)
	
	if (openid && token) { 
		return true
	}
	
	return false
}

